@mixin section-margin($margin: 8.05%) {
  margin-left: $margin;
  margin-right: $margin;
  width: calc(100% - #{$margin * 2});

  @include portrait {
    margin-left: $lem;
    margin-right: $lem;
    width: calc(100% - #{$lem * 2});
  }

  @include tablet-landscape {
    margin-left: $lem * 2;
    margin-right: $lem * 2;
    width: calc(100% - #{$lem * 4});
  }
}
