@mixin advertisers__articles--mobile {
  @include mobile {
    .article {
      flex-basis: 100%;
      margin-right: 0;
      margin-bottom: $lem + $lem / 2;

      .article__category {
        margin-bottom: $lem + $lem / 2;
      }

      .article__subtitle-with-image {
        padding: $lem 0;
      }

      &:last-of-type {
        margin-top: 0;
      }
    }
  }
}
