@mixin features__filters {
  @include section-margin;

  .ul {
    @include ul;
    @include ul--horizontal;
    justify-content: center;

    .filter {
      @include filter;
      @include text-style(navigation);
      margin-right: $lem * 1.458;
    }
  }

  @include features__filters--mobile;
  @include features__filters--portrait;
}
