@mixin rich-media-hero__icon {
  @include transition(transform);
  @include perspective(500px, horizontal, '.figure');

  .figure {
    @include figure;
    width: $lem * 5;
    transform: rotateX(60deg);
  }

  @include rich-media-hero__icon--mobile;
  @include rich-media-hero__icon--tablet;
  @include rich-media-hero__icon--landscape;
  @include rich-media-hero__icon--desktop;
  @include rich-media-hero__icon--fullhd;
}
