@mixin web-interface {
  @include colors(white-on-black);

  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

  &__slides {
    @include web-interface__slides;
  }

  &__description {
    @include web-interface__description;
  }

  @include web-interface__grid;
}
