@mixin advertisers--landscape {
  @include landscape {
    padding-top: $lem * 8;

    .articles {
      width: $text-block-width;
      margin-left: $lem * 2.25 + $lem / 2;
      order: 2;
    }

    .advertisers__mockups {
      order: 1;
    }

    .advertisers__slider {
      order: 3;
      margin-top: $lem * 2.33;
    }
  }
}
