@mixin rich-media--tablet-and-below {
  @include tablet-and-below {
    padding-top: $lem * 6;

    .articles {
      @include articles;
      width: auto;
      margin-top: $lem;

      .article {
        &__subtitle {
          min-height: $lem * 2;
          line-height: 1.25;
        }

        // overwrite landscape
        &:nth-of-type(1),
        &:nth-of-type(2),
        &:nth-of-type(3) {
          left: 0;
          margin-right: $lem;
        }

        &:nth-of-type(3) {
          margin-right: 0;
        }
      }
    }
  }
}
