@mixin menu__items--vertical {
  display: flex;
  flex-wrap: wrap;

  @include colors(white-on-black);
  opacity: .99;

  .menu-item {
    flex-basis: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    @include border(normal, bottom);

    @include scale(large-2);
    @include mobile {
      @include scale(large-1);
    }
  }
}
