@mixin slider2__navigation($button-color) {
  display: flex;
  justify-content: space-between;
  align-items: center;

  position: absolute;
  width: 100%;
  // height 0, otherwise that part of the underlying image occupied by the navigation is not clickable
  height: 0;

  .button {
    @include colors($button-color);

    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    .saquo {
      @include saquo($size: $lem / 2);
    }

    &--right {
      margin-right: $lem * 1.583;
    }

    &--left {
      margin-left: $lem * 1.583;
    }

    &--inactive {
      opacity: .5;
      cursor: default;
    }
  }

  @include slider2__navigation--mobile;
}
