@mixin article {
  display: flex;
  flex-direction: column;

  position: relative;

  &__title {
    @include article__title;
  }

  &__subtitle {
    @include article__subtitle;
  }

  &__thumb {
    @include article__thumb;
  }

  &__excerpt {
    @include article__excerpt;
  }

  &__category {
    @include article__category;
  }

  &__button {
    @include article__button;
  }

  &__button-w-icon {
    @include article__button-w-icon;
  }

  &__cta {
    @include article__cta;
  }

  &__icon {
    @include article__icon;
  }

  &__mockup {
    @include article__mockup;
  }

  &__list {
    @include article__list;
  }

  &__subtitle-with-image {
    @include article__subtitle-with-image;
  }

  &--horizontal {
    @include article--horizontal;
  }

  &--horizontal-switched {
    @include article--horizontal-switched;
  }


  @include transition(opacity);
  &--inactive {
    @include article--inactive;
  }
}
