// Transition
//
// Smooth animation transition using a cubic-bezier function following the recommendations from [Google Web Fundamentals](https://developers.google.com/web/fundamentals/look-and-feel/animations/animating-between-views).
// Used for all animations like the slider or hovering an element.
// - https://material.google.com/motion/duration-easing.html#duration-easing-dynamic-durations
//
//
// Styleguide transition
@mixin transition($change) {
  will-change: $change;

  @include mobile {
    transition: $change 0.375s cubic-bezier(0.4, 0.0, 0.2, 1);
  }

  @include landscape {
    transition: $change 0.200s cubic-bezier(0.4, 0.0, 0.2, 1);
  }

  @include tablet {
    transition: $change 0.487s cubic-bezier(0.4, 0.0, 0.2, 1);
  }
}
