@mixin home--gradients {
  .container {
    position: relative;

    .header {
      position: absolute;
      z-index: 10;
    }

    .hero {
      position: relative;

      @include portrait {
        background-image: radial-gradient(
          circle farthest-side,
          $blue,
          $black
        );
      }

      @include landscape {
        background-image: radial-gradient(
          ellipse closest-side, 
          $blue,
          $black
        );
      }
    }

    .publishers {
      background-image: linear-gradient(
        $black,
        $gray-footer
      );
    }

    .advertisers {
      background-image: linear-gradient(
        $gray-footer,
        $blue
      );
    }

    .rich-media {
      background-image: linear-gradient(
        $blue,
        $green
      );
    }

    .everything {
      background-image: linear-gradient(
        $green,
        $green-dark
      );
    }
  }
}
