@mixin publishers__articles {
  .article {
    @include article;

    &__title {
      padding: $lem / 3 0;
    }

    &__subtitle-with-image {
      padding-top: $lem;
    }

    &__category {
      @include colors(green-on-transparent);
    }

    &__excerpt {
      @include colors(gray-format-on-transparent);
    }
  }

  @include publishers__articles--mobile;
  @include publishers__articles--tablet-and-below;
}
