@mixin slider2--size($width, $margin, $button-width, $figcaption-height, $left-offset) {
  $intrinsic-ratio: 1;

  // $height: intrinsic ratio + figcaption height
  $height: calc(#{$width} / #{$intrinsic-ratio} + #{$figcaption-height});

  // $top: image height / 2
  $top: calc((#{$width} / #{$intrinsic-ratio}) / 2);

  // push left: width - button margin left - button size
  // - or: align the second slide to the grid above (10x)
  $left: $width - $lem * 1.583 - $lem * 1.749 - $left-offset;

  height: $height;
  overflow: hidden;

  &__slides {
    .slide {
      width: $width;

      .img {
        width: $width;
        height: auto;
      }

      @for $i from 1 to 11 {
        &:nth-of-type(#{$i}) {
          transform: translateX(($width + $margin) * ($i - 1) - $left);
        }
      }
    }
  }

  &__navigation {
    top: $top;

    .button {
      @include circle($button_width);
    }
  }
}
