@mixin hero__article {
  @include article;
  @include article--centered;

  margin-top: $lem * 6.456;

  &__title {
    @include text-style(hero);
  }

  &__subtitle {
    @include colors(gray-format-on-transparent);

    max-width: none;
    margin-top: $lem / 3;
    letter-spacing: 0.035em;
  }

  @include hero__article--mobile;
}
