@mixin slider2--grid {
  position: relative;

  @include slider2--size($lem * 11.33, $lem * 0.833, $lem * 1.749, $lem * 2, $lem / 4);

  @include desktop {
    @include slider2--size($lem * 11.33, $lem * 0.833, $lem * 1.749, $lem * 2, $lem);
  }

  @include mobile {
    @include slider2--size($lem * 6, $lem / 2, $lem * 2, $lem * 3, $lem / 4);
  }
}
