@mixin hero-mockup--reposition {
  &--tablet {
    top: $lem * 10.662;
  }

  &--phone {
    transform: translateY($lem * 15.454);
  }

  @include mobile {
    &--laptop {
      padding: 0 $lem;
    }

    &--tablet {
      top: $lem * 2.698;

      .figure {
        .img {
          width: calc((100vw - #{$lem * 2}) * 60.95 / 100);
        }
      }
    }

    &--phone {
      transform: translateY($lem * 3.925);

      .figure {
        .img {
          width: calc((100vw - #{$lem * 2}) * 19.12 / 100);
        }
      }
    }
  }

  @include mobile-large {
    &--tablet {
      top: $lem * 3.541;
    }

    &--phone {
      transform: translateY($lem * 5.151);
    }
  }

  @include tablet {
    &--tablet {
      top: 9em;
    }

    &--phone {
      transform: translateY(13em);
    }
  }

  @include tablet-landscape {
    &--laptop {
      .figure {
        .img {
          width: 859px;
        }
      }
    }

    &--tablet {
      .figure {
        .img {
          width: 524px;
        }
      }
    }

    &--phone {
      .figure {
        .img {
          width: 164px;
        }
      }
    }
  }

  @include desktop {
    &--tablet {
      top: $lem * 12.139;
    }

    &--phone {
      transform: translateY($lem * 17.661);
    }
  }

  @include fullhd {
    &--tablet {
      top: 15em;
    }

    &--phone {
      transform: translateY(22em);
    }
  }
}
