@mixin publishers__articles--tablet-and-below {
  @include tablet-and-below {
    width: 100% !important;
    margin-left: 0 !important;

    @include grid(2, $lem, 0px);

    .article {
      &:last-of-type {
        margin-top: .1em;

        .article__subtitle-with-image {
          padding-bottom: $lem / 4;
        }
      }
    }
  }
}
