@mixin publishers__mockups--tablet-portrait {
  @include tablet-portrait {
    height: calc(56.24vw + #{$lem * 3});
    margin: $lem 0;

    align-items: center;
    justify-content: center;

    .mockup {
      &--android {
        @include mockup--android(7em);
      }

      &:first-of-type {
        transform: scale(1) translateX($lem) translateY(10em);
      }

      &:last-of-type {
        transform: translateX($lem * 2);

        .mockup__iframe {
          @include mockup__iframe(85vw, 16, 9);
        }
      }
    }
  }
}
