@mixin web-interface__grid {
  position: relative;

  &__slides,
  &__description {
    position: relative;
  }

  .slides {
    position: relative;
    width: 100%;

    .article {
      position: absolute;
      width: 45vw;
      z-index: 1;

      &__button {
        top: -$lem * 4;
      }

      &:nth-of-type(1) {
        left: -15vw;

        .article__button {
          left: $lem * 3.499;
        }
      }

      &:nth-of-type(2) {
        top: -$lem * 2.749;
        left: 22.5vw;
        width: 55vw;

        .img {
          width: 55vw;
        }

        z-index: 2;
      }

      &:nth-of-type(3) {
        right: -15vw;

        .article__button {
          left: -$lem * 3.499;
        }
      }
    }
  }

  @include web-interface__grid--portrait;
  @include web-interface__grid--mobile;
  @include web-interface__grid--mobile-large;
  @include web-interface__grid--landscape;
  @include web-interface__grid--tablet-landscape;
  @include web-interface__grid--desktop;
  @include web-interface__grid--fullhd;
}
