@mixin web-interface__grid--fullhd {
  @include fullhd {
    .web-interface__slides {
      top: -$lem * 6;

      .slides {
        .article {
          &:nth-of-type(2) {
            top: -$lem * 2.749 - $lem / 2;
          }
        }
      }
    }

    .web-interface__description {
      top: -$lem * 6.165;
    }
  }
}
