@mixin hero__sliders {
  position: relative;

  $w: $lem * 15;
  $b: $lem * 1.5;

  .range {
    @include transition(opacity);
    visibility: hidden;

    &.device-range-horizontal {
      @include range($w, $b);
      margin-top: $lem * 3;
    }

    &.device-range-vertical {
      @include range($w, $b, vertical);

      position: absolute;
      right: $lem * 6;
      top: $lem * 24;
    }
  }

  @include hero__sliders--mobile;
}
