@mixin rich-media-hero__video {
  @include rich-media-hero--reposition(1200px, .73, -39em, 3.7em, '.video');

  .video {
    @include video;
    width: 22em;
  }

  @include rich-media-hero__video--mobile;
  @include rich-media-hero__video--tablet-landscape;
  @include rich-media-hero__video--tablet;
  @include rich-media-hero__video--desktop;
  @include rich-media-hero__video--fullhd;
}
