@mixin everything__articles {
  @include articles($margin-right: $lem * 1.666);
  @include section-margin;

  margin-top: $lem * 3.457;

  .article {
    @include colors(black-on-white);
    justify-content: space-between;

    &__icon {
      .icon {
        @include icon($lem * 2.166, $lem * 2.166, $green);
      }
    }

    &__excerpt {
      max-width: 14em;
    }

    &__button {
      padding: $lem * 2 0 $lem;

      .button {
        @include colors(white-on-red);
      }
    }
  }

  @include everything__articles--landscape;
  @include everything__articles--tablet-and-below;
  @include everything__articles--tablet-portrait;
  @include everything__articles--mobile;
}
