@mixin team--tablet-and-below {
  @include tablet-and-below {
    // The text above is moved up with $lem * 4
    padding-top: ($lem * 10 - $lem * 4) / 2;

    .articles {
      flex-wrap: nowrap;
      
      .article {
        &__subtitle {
          min-height: $lem * 2;
        }

        &:nth-of-type(2) {
          margin-right: $lem;
        }
      }
    }
  }
}
