@mixin publishers--landscape {
  @include landscape {
    margin-top: $lem * 8;

    
    .articles {
      width: $text-block-width;
      // icon aligned to section left border
      margin-left: ($lem * 2.25 + $lem / 2) * 2;
      order: 1;
    }

    .publishers__mockups {
      order: 2;
      left: ($lem * 6 / 2);
    }

    .publishers__slider {
      order: 3;
      // 2.2em is the transformY of the browser mockup aligned to 'Publishers'
      margin-top: $lem * 2.33 + 2.2em;
    }
  }
}
