@mixin everything {
  @include colors(white-on-green);

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &__hero {
    @include everything__hero;
  }

  .articles {
    @include everything__articles;
  }

  @include everything--landscape;
  @include everything--tablet-and-below;
  @include everything--mobile;
}
