@mixin advertisers__mockups--mobile {
  @include mobile {
    height: calc(56.24vw + #{$lem * 2});

    .mockup {
      height: $lem;

      .mockup--browser {
        .mockup__buttons {
          padding: $lem / 6 0;

          .circle {
            @include circle($lem / 8);
          }
        }
      }
    }
  }
}
