@mixin footer-bottom__made-with {
  .made-with {
    @include text-centered;
    @include colors(gray-footer-on-white);
    @include text-style(section-title);

    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;

    .sprite--made-with-muscle {
      @include retina-sprite($made-with-muscle-group);
      display: flex;
      margin: 0 $lem / 2;
    }
  }

  .footer-bottom__copyright {
    @include footer-bottom__copyright;
    margin-top: $lem / 4;

    @include mobile-and-up {
      display: flex;
    }
  }
}
