@mixin features--gradients {
  .container {
    position: relative;

    .header {
      position: absolute;
      z-index: 10;
    }

    .features__hero {
      background-image: linear-gradient(
        $black,
        $blue
      );
    }

    .features__articles {
      background-image: linear-gradient(
        $blue,
        $white 20%
      );
    }
  }
}
