// Home page
//
// The homepage
// The header and footer are shared with other pages. See the default template.
//
// Styleguide home
html {
  &.home {
    body {
      @include home--gradients;
      @include home--popups;

      .menu {
        .menu-item {
          &:nth-of-type(1) {
            .link {
              @include link(white-on-transparent, white-on-transparent, white-on-transparent);
            }
          }
        }
      }

      .hero {
        @include hero;
        //display: none;
      }

      .publishers {
        @include publishers;
        //display: none;
      }

      .advertisers {
        @include advertisers;
        //display: none;
      }

      .rich-media {
        @include rich-media;
        //display: none;
      }

      .everything {
        @include everything;
        //display: none;
      }

      .web-interface {
        @include web-interface;
        //display: none;
      }

      .web-interface-form {
        @include web-interface-form;
      }

      .team {
        @include team;
      }
    }
  }
}
