@mixin mockup--browser($button-colors: false) {
  display: flex;
  flex-wrap: wrap;

  .mockup__buttons {
    @include mockup__buttons($button-colors);

    flex-basis: 100%;

    @include border(thin, all);
    border-bottom: none;
    border-radius: $lem / 4 $lem / 4 0 0;

    @if ($button-colors != false) {
      border-color: $gray-format;
    }
  }

  .figure {
    @include figure($stretch: stretched);
    @include border(thin, all);

    line-height: 0;
    width: 100%;
  }

  .mockup__iframe {
    @include mockup__iframe(40vw, 16, 9);
  }
}
