@mixin web-interface__grid--portrait {
  @include portrait {
    .web-interface__slides {
      top: -$lem * 8;
      min-height: calc(50vw / 0.75);

      .slides {
        .article {
          .article__button {
            transform: scale(.8);
          }

          &:nth-of-type(1) {
            .article__button {
              left: $lem;
            }
          }

          &:nth-of-type(3) {
            .article__button {
              right: $lem;
              left: auto;
            }
          }
        }
      }
    }

    .web-interface__description {
      top: -$lem * 5;
    }
  }
}
