@mixin publishers__mockups--tablet-landscape {
  @include tablet-landscape {
    $w: 85vw;
    width: $w;
    height: calc(#{$w} / 1.778 + #{$lem * 2 * 2});

    .mockup {
      &:first-of-type {
        width: 9.1em;
        transform: translateY(14em) translateX($lem);
      }

      &:last-of-type {
        transform: translateX($lem * 2.5) translateY(2.2em);

        .mockup__iframe {
          @include mockup__iframe($w, 16, 9);
        }
      }
    }
  }
}
