@mixin publishers__mockups--desktop {
  @include desktop {
    $w: 60vw;

    .mockup {
      &:first-of-type {
        transform: scale(1.15) translateY(12em) translateX($lem + $lem / 2);
      }

      &:last-of-type {
        .mockup__iframe {
          @include mockup__iframe($w, 16, 9);
        }
      }
    }
  }
}
