@mixin footer-bottom {
  margin-top: $lem * 2;

  > * {
    @include text-centered;
  }

  .sprite--techstars {
    @include retina-sprite($tech-stars-group);
  }

  &__made-with {
    @include footer-bottom__made-with;
  }

  .icon {
    @include icon($lem * 4.32, $lem * 2.25, $gray-footer);
    text-align: right;
  }

  &__copyright {
    @include footer-bottom__copyright;
  }

  @include footer-bottom--mobile;
  @include footer-bottom--tablet;
  @include footer-bottom--landscape;
}
