@mixin article--horizontal-switched {
  @include mobile-and-up {
    .article__thumb {
      margin-right: 0;
      order: 2;
    }

    .article-container {
      margin-left: $lem * 4.165;
      margin-right: $lem * 4.165;
    }
  }

  @include tablet-portrait {
    .article-container {
      margin-left: 0;
    }
  }
}
