@mixin mockup--android($width: 8.3em, $top: 9.2%, $left: 5%, $padding: 178%) {
  .figure {
    @include figure;
  }

  .mockup__iframe {
    @include mockup__iframe($width, 2, 3);

    width: auto;
    top: $top;
    left: $left;
    position: absolute;

    .intrinsic-ratio-container {
      padding-bottom: $padding;
    }
  }
}
