@mixin slide2($border-color) {
  position: absolute;
  left: 0;
  top: 0;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;;

  opacity: .5;
  cursor: pointer;
  @include transition(transform);

  .picture {
    @include border(thin, all);
    border-color: transparent;
    padding: $lem / 4;
    line-height: 0;
  }

  &--active,
  &:hover {
    @include slide2--active($border-color);
  }
}
