@mixin publishers__mockups--landscape {
  @include landscape {
    $w: 55vw;
    width: $w;
    height: calc(#{$w} / 1.778 + #{$lem * 2 * 2});

    .mockup {
      &:first-of-type {
        width: 9.1em;
        transform: translateY($lem * 6.248 + 2.2em) translateX($lem);
      }

      &:last-of-type {
        transform: translateX($lem * 5) translateY(2.2em);

        .mockup__iframe {
          @include mockup__iframe($w, 16, 9);
        }
      }
    }
  }
}
