@mixin team--mobile {
  @include mobile {
    padding-top: $lem * 2;
    padding-bottom: $lem * 2;

    .articles {
      margin-top: $lem;
      flex-wrap: wrap;

      .article {
        margin-bottom: $lem;

        &__subtitle {
          min-height: $lem;
        }

        &__thumb {
          margin-bottom: $lem / 2;
        }
      }
    }
  }
}
