@mixin footer-bottom--mobile {
  @include mobile {
    padding-top: $lem * 2;

    @include grid(1, 0px, $lem);

    > * {
      min-height: $lem * 2.25;
    }

    flex-direction: column;
    justify-content: center;

    .sprite {
      display: flex;
      justify-content: center;
    }

    .icon {
      text-align: center;
    }

    &__made-with {
      display: flex;
      justify-content: center;
    }

    &__copyright {
      display: flex;
      margin-top: $lem;
    }
  }
}
