@mixin team {
  padding-bottom: $lem * 4.165;

  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

  &__hero {
    @include team__hero;
  }

  .articles {
    @include articles(4, $lem * 4);
    @include section-margin;

    margin-top: $lem * 3;
    align-items: flex-start;
    flex-wrap: nowrap;

    .article {
      > * {
        order: 4;
      }

      &__thumb {
        order: 1;
        margin-bottom: $lem;
      }

      &__subtitle {
        order: 2;
      }

      &__category {
        order: 3;
        padding-top: $lem / 2;
      }

      &__excerpt {
        padding-top: $lem;
        @include colors(gray-team-on-black);
        @include scale(section-title);
      }
    }
  }

  @include team--landscape;
  @include team--tablet-and-below;
  @include team--tablet-landscape;
  @include team--mobile;
}
