@mixin footer {
  display: flex;
  flex-direction: column;

  padding-top: $lem * 4.166;
  padding-bottom: $lem * 5.748;

  @include colors(black-on-white);
  @include section-padding;

  .footer-top {
    @include footer-top;
  }

  .footer-bottom {
    @include footer-bottom;
  }

  @include footer--mobile;
}
