@mixin input {
  @include text-style(body-text);
  max-width: none;
  width: 100%;

  @include border(normal, all);
  border-radius: $lem / 4;
  background: transparent;
  padding: $lem * 0.4 $lem * 0.58;
  box-sizing: border-box;

  margin-bottom: $lem * 1.25;

  @include colors(gray-footer-on-transparent);
  @include transition(color);

  &:focus,
  &:hover {
    color: $white;
  }
}
