@mixin contact--portrait {
  @include portrait {
    .ul {
      @include ul--vertical;
      align-items: flex-start;

      .li {
        &.phone {
          margin-top: 0;
        }
      }

      .bull {
        display: none;
      }
    }

    .articles {
      margin-top: $lem / 4;
    }
  }
}
