@mixin hero-mockup {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;

  .figure {
    @include figure;
  }

  &--laptop {
    z-index: 1;
  }

  &--tablet {
    z-index: 2;
  }

  &--phone {
    z-index: 3;
  }

  @include hero-mockup--reposition;
}
