@mixin publishers--tablet-and-below {
  @include tablet-and-below {
    flex-wrap: wrap;
    align-items: flex-start;
    overflow-x: hidden;

    margin-top: $lem * 4;

    .articles {
      @include section-padding;
      order: 1;
    }

    .publishers__mockups {
      @include section-padding;
      order: 2
    }

    .publishers__slider {
      order: 3;
    }
  }
}
