@mixin article__subtitle-with-image {
  display: flex;
  align-items: center;

  $icon-width: $lem * 2.25;

  @include article__subtitle-with-image--landscape($icon-width);

  .article__thumb {
    @include article__thumb;
    margin-right: $lem / 2;

    .img {
      width: $icon-width;
    }
  }

  .article__subtitle {
    @include article__subtitle;
  }
}
