@mixin features__articles {
  @include colors(black-on-white);
  @include section-padding;

  display: flex;
  flex-direction: column;

  .article {
    @include article;
    @include article--horizontal;

    &__thumb {
      .figure {
        @include colors(white-on-black);

        .img {
          width: $lem * 24.326;
        }
      }
    }

    .article-container {
      // margin-top: $lem * 1.625; - will be aligned by flexbox
    }

    &__excerpt {
      // padding-top: $lem * 1.125;
      padding-top: $lem / 2;
    }

    &__list {
      @include ul--with-bullet($blue);
      // margin-top: $lem * 1.5;
      margin-top: $lem;

      .li {
        + .li {
          //margin-top: $lem * .75;
          margin-top: $lem / 2;
        }
      }
    }

    &__excerpt,
    &__list {
      @include colors(gray-footer-on-transparent);
    }
  }

  .horizontal-line {
    @include horizontal-line($lem * 33, $gray-format);
    padding: $lem * 4.165;

    &:last-of-type {
      display: none;
    }
  }

  @include features__articles--desktop;
  @include features__articles--fullhd;
  @include features__articles--portrait;
  @include features__articles--mobile;
}
