@mixin web-interface__description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .article {
    @include article;
    @include article--centered;


    &__excerpt {
      margin-top: $lem;
      margin-bottom: $lem * 2.166;
      max-width: 31em;
    }

    &:last-of-type {
      .article__cta {
        order: 1;
      }

      .article__button-w-icon {
        margin-top: $lem;
        order: 2;

        .button {
          @include colors(white-on-red);

          .img {
            width: $lem;
          }
        }
      }
    }
  }

  @include web-interface__description--mobile;
}
