@mixin web-interface-form {
  max-width: $lem * 20;

  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .icon {
    @include icon($lem * 1.666, $lem * 1.666, $white);
    margin: $lem * 2 0;
    order: 1;
  }

  .title,
  .form {
    order: 2;
  }

  .title {
    display: flex;
    justify-content: center;
    flex-basis: 100%;

    @include text-style(headline-1);
    text-align: center;
  }

  .form {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    flex-basis: 100%;

    margin: $lem * 1.25 0;

    @include form;

    .fieldset {
      display: flex;
      flex-wrap: wrap;

      .first-name,
      .last-name {
        width: calc((100% - #{$lem * 1.666}) / 2 - 1px);
      }

      .first-name {
        margin-right: $lem * 1.666;
      }
    }

    .agreement {
      @include scale(small-1);
      @include colors(gray-footer-on-transparent);
      text-align: center;
    }

    .button {
      @include button;
      @include colors(white-on-red);

      border: none;
      margin: $lem * 1.25 0;
    }
  }
}
