@mixin contact {
  display: flex;
  flex-direction: column;

  .ul {
    @include ul--horizontal;
    justify-content: flex-start;
    align-items: center;

    .li {
      &.phone {
        @include colors(gray-footer-on-white);

        .bull {
          padding: 0 $lem / 2;
        }
      }
    }
  }

  .articles {
    @include articles(2, $lem * 4);
    margin-top: 1em;

    .article {
      &__thumb {
        order: 2;
        margin-top: $lem / 2;
      }

      &__title {
        @include text-style(footer);
        order: 1;
      }
    }
  }

  @include contact--portrait;
  @include contact--mobile;
}
