// Scale
//
// Typographic grid and scale.
// Different font sizes based on the [Modular Scale 4:3 Perfect Fourth](http://www.modularscale.com/?1&em&1.333&web&text) settings.
//
// Styleguide scale

// The default metric for sizing non-typographic elements
// - it is based on the default font size and line height
// - $lem is used for spacing elements on the webpage like margins and padding

$lem: 1.263em;




// Different typographic scales
@mixin scale($variant) {

  // The default font size and line height
  @if ($variant == 'default') {
    font-size: 118.7%;
    line-height: 1.263;
    
    
  }

  // Font size smaller with 1 unit
  @if ($variant == 'small-1') {
    font-size: 0.737em;
    
    
  }

  // Font size larger with 1 unit
  @if ($variant == 'large-1') {
    font-size: 1.053em;
    
    
  }

  // Font size larger with 2 units
  @if ($variant == 'large-2') {
    font-size: 1.368em;
    
    
  }

  // Font size larger with 3 units
  @if ($variant == 'large-3') {
    font-size: 1.895em;
    
    
  }

  // Font size larger with 4 units
  @if ($variant == 'large-4') {
    font-size: 3.368em;
    
    
  }

  // Font size for section title
  @if ($variant == 'section-title') {
    font-size: 0.895em;
    
    
  }

}


// Typographic scale classes

.scale--default {
  font-size: 118.7%;
  line-height: 1.263;
  
  
}

.scale--small-1 {
  font-size: 0.737em;
  
  
}

.scale--large-1 {
  font-size: 1.053em;
  
  
}

.scale--large-2 {
  font-size: 1.368em;
  
  
}

.scale--large-3 {
  font-size: 1.895em;
  
  
}

.scale--large-4 {
  font-size: 3.368em;
  
  
}

.scale--section-title {
  font-size: 0.895em;
  
  
}

