@mixin footer-links {
  &__title {
    display: flex;

    @include text-style(headline-2);
    font-weight: normal;
  }

  .ul {
    @include ul;
    padding-top: $lem;

    .li {
      padding-bottom: $lem / 4;

      .link {
        @include link(blue-on-white, blue-on-white, blue-on-white);
        @include text-style(body-text);
      }
    }
  }
}
