@mixin rich-media--landscape {
  @include landscape {
    padding-top: $lem * 8;
    
    .articles {
      @include articles($margin-right: $lem * 1.66);
      flex-wrap: nowrap;

      .article {
        margin-right: 0;
        position: relative;

        &:nth-of-type(1) {
          left: 1em;
        }

        &:nth-of-type(2) {
          left: -$lem * 2;
        }

        &:nth-of-type(3) {
          left: -$lem * 3;
        }


        &__excerpt {
          max-width: 14.4em;
        }
      }
    }
  }
}
