@mixin hero__mockups {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 100%;
  position: relative;

  .hero-mockup {
    @include hero-mockup;
  }

  @include hero__mockups--mobile;
  @include hero__mockups--mobile-large;
  @include hero__mockups--tablet;
  @include hero__mockups--landscape;
  @include hero__mockups--tablet-landscape;
  @include hero__mockups--desktop;
  @include hero__mockups--fullhd;
}
