@mixin web-interface__grid--tablet-landscape {
  @include tablet-landscape {
    .slides {
      .article {
        &:nth-of-type(2) {
          top: -$lem * 2;
        }

        &:nth-of-type(1) {
          .article__button {
            left: $lem * 2;
          }
        }

        &:nth-of-type(3) {
          .article__button {
            right: $lem * 2;
            left: auto;
          }
        }
      }
    }
  }
}
