@mixin web-interface__grid--mobile {
  @include mobile {
    padding-top: 20vh;

    .web-interface__slides {
      .slides {
        .article {
          .article__button {
            transform: scale(.6);
            top: -$lem * 3;
          }

          &:nth-of-type(2) {
            top: -$lem - $lem / 2;
          }
        }
      }
    }

    .web-interface__description {
      top: -$lem * 2;
    }
  }
}
