@mixin features__hero {
  // The height of the header must be added here, since the header has position: absolute
  margin-top: $lem * 3;

  // Positioning title
  padding: $lem * 6.706 0;

  display: flex;
  flex-direction: column;
  align-items: center;

  .title {
    display: flex;

    @include text-style(hero);
    @include text-centered;
  }

  .horizontal-line {
    @include horizontal-line($lem * 10.33, $black-features-hero-line);
    padding: $lem * 2.3 0;
  }

  .features__filters {
    @include features__filters;
  }

  @include features__hero--mobile;
}
