@mixin advertisers {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;

  .articles {
    @include advertisers__articles;
  }

  &__mockups {
    @include advertisers__mockups;
  }

  &__slider {
    @include advertisers__slider;
  }

  @include advertisers--landscape;
  @include advertisers--tablet-and-below;
  @include advertisers--mobile;
}
