//http://stackoverflow.com/questions/16094785/have-a-fixed-position-div-that-needs-to-scroll-if-content-overflows

@mixin popup {
  @include colors(white-on-black);

  position: fixed;
  width: 100%;
  top: 0;
  bottom: 0;
  overflow-x: hidden;
  overflow-y: scroll;

  justify-content: center;
  align-items: center;

  display: none;
  opacity: 0;
  @include transition(opacity);

  .icon-hamburger {
    @include icon-hamburger;

    position: absolute;
    top: 1vw;
    right: 1vh;
  }

  &__content {
    height: 100%;
  }


  &--active {
    @include popup--active;
  }

}
