@mixin advertisers--tablet-and-below {
  @include tablet-and-below {
    flex-wrap: wrap;
    align-items: flex-start;
    overflow-x: hidden;

    padding-top: $lem * 6;

    .articles {
      @include section-padding;
      order: 1;
    }

    .advertisers__mockups {
      @include section-padding;
      order: 2
    }

    .advertisers__slider {
      order: 3;
    }
  }
}
