@mixin rich-media {
  @include colors(white-on-green);

  display: flex;
  flex-direction: column;
  justify-content: center;

  &__headline {
    @include rich-media__headline;
  }

  .rich-media-hero {
    @include rich-media-hero;
  }

  .articles {
    @include section-margin;
    justify-content: center;

    .article {
      &__subtitle {
        margin-top: $lem;
        margin-bottom: $lem / 2;
      }
    }
  }

  @include rich-media--landscape;
  @include rich-media--desktop;
  @include rich-media--fullhd;
  @include rich-media--tablet-and-below;
  @include rich-media--mobile;
}
