@mixin publishers__mockups--mobile {
  @include mobile {
    height: calc(56.24vw + #{$lem});
    margin: $lem 0;

    .mockup {
      &--android {
        @include mockup--android(3.8em);

        .mockup__iframe {
          width: 92%;
        }
      }

      &:first-of-type {
        transform: scale(.6) translateX(0em) translateY(3.3em);
      }

      &:last-of-type {
        transform: translateX($lem);

        .mockup--browser {
          .mockup__buttons {
            padding: $lem / 6 0;

            .circle {
              @include circle($lem / 8);
            }
          }

          .mockup__iframe {
            @include mockup__iframe(85vw, 16, 9);
          }
        }
      }
    }
  }
}
