@mixin section-padding($margin: 8.05%) {
  padding-left: $margin;
  padding-right: $margin;
  width: calc(100% - #{$margin * 2});

  @include portrait {
    padding-left: $lem;
    padding-right: $lem;
    width: calc(100% - #{$lem * 2});
  }

  @include tablet-landscape {
    padding-left: $lem * 2;
    padding-right: $lem * 2;
    width: calc(100% - #{$lem * 4});
  }
}
