@mixin range__button($button-size, $line-height, $direction) {
  @include circle($button-size);
  @include colors(gray-format-on-white);

  display: flex;
  box-shadow: 1px 1px 5px #174b56;
  cursor: pointer;

  @if ($direction == horizontal) {
    position: relative;
    top: -$button-size / 2 - $line-height / 2;
  } @else {
    position: absolute;
    top: 0;
    left: -$line-height;
  }
}
