@mixin web-interface__slides {
  width: 100%;

  .article {
    @include article;
    @include article--centered;
    position: relative;

    &__title {
      display: none;
    }

    &__mockup {
      .mockup {
        @include mockup($button-colors: true);
        @include mockup--placeholder;
      }
    }

    &__button {
      position: relative;
      top: -$lem * 3;
      z-index: 2;

      .button {
        @include colors(white-on-blue);
      }
    }
  }
}
