// Range
//
// Input control range
//
// Styleguide range
@mixin range($width, $button-size, $direction: horizontal) {
  position: relative;

  $w: $width;
  $h: $width / 30;
  $l: $h;

  @if ($direction == vertical) {
    $h: $width;
    $w: $width / 30;
    $l: $w;
  }

  .range__line {
    @include range__line($w, $h);
  }

  .range__button {
    @include range__button($button-size, $l, $direction);
  }
}
