// http://stackoverflow.com/questions/5306640/how-to-set-bullet-colors-in-ul-li-html-lists-via-css-without-using-any-images-or
@mixin ul--with-bullet($color: $black) {
  padding: 0;
  margin: 0;

  .li {
    display: flex;
    align-items: center;

    &__bullet {
      color: $color;
      transform: scale(2);
    }

    &__text {
      margin-left: $lem / 2;
    }
  }
}
