// Breakpoints
//
// Used to adapt content and design to devices.
//
// Styleguide breakpoints


// Screens smaller than 768px
$breakpoint-mobile: 768px;

// Screens larger than 400px and smaller than 768px
$breakpoint-mobile-large: 400px;

// Screens bigger than 768px and smaller than 1024px
$breakpoint-tablet: 1024px;

// Screens larger than 1600p and smaller than 1920pxx
$breakpoint-desktop: 1600px;

// Screens larger than 1920px
$breakpoint-fullhd: 1920px;

// Retina displays
$breakpoint-retina: n/a;




@mixin retina {
  @media only screen and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
    @content
  }
}

@mixin portrait {
  @media only screen and (orientation: portrait) {
    @content
  }
}

@mixin landscape {
  @media only screen and (orientation: landscape) {
    @content
  }
}

@mixin mobile {
  @media only screen and (max-width: $breakpoint-mobile - 0.1) {
    @content
  }
}

@mixin mobile-large {
  @media only screen and (min-width: $breakpoint-mobile-large) and (max-width: $breakpoint-mobile - 0.1) {
    @content
  }
}

@mixin mobile-and-up {
  @media only screen and (min-width: $breakpoint-mobile) {
    @content
  }
}

@mixin tablet {
  @media only screen and (min-width: $breakpoint-mobile) and (max-width: $breakpoint-tablet) {
    @content
  }
}

@mixin tablet-and-below {
  @media only screen and (max-width: $breakpoint-tablet) {
    @content
  }
}

@mixin tablet-portrait {
  @media only screen and (min-width: $breakpoint-mobile) and (max-width: $breakpoint-tablet) and (orientation: portrait) {
    @content
  }
}

@mixin tablet-landscape {
  @media only screen and (min-width: $breakpoint-mobile) and (max-width: $breakpoint-tablet) and (orientation: landscape) {
    @content
  }
}

@mixin laptop {
  @media only screen and (min-width: $breakpoint-tablet + 0.1) and (max-width: $breakpoint-desktop - 0.1) {
    @content
  }
}

@mixin desktop {
  @media only screen and (min-width: $breakpoint-desktop) and (max-width: $breakpoint-fullhd - 0.1) {
    @content
  }
}

@mixin fullhd {
  @media only screen and (min-width: $breakpoint-fullhd) {
    @content
  }
}

